<template>
  <div class="hs-container">
    <c-header isBackNav :title="Number($route.query.TravelType) === 0 ? $t('base.BusinessTrip') : $t('base.PersonalTrip')" fixedBackPath="/home">
      <p class="c-header-right" slot="right" v-if="Number($route.query.TravelType) === 0" @click="showTravelStandard = true">{{$t('base.Policy')}}</p>
    </c-header>
    <hs-scroll ref="tableView" :data="[details]" class="tableView">
      <ul>
        <div class="slider-box">
          <hs-swiper maxType="Width" :images="banners"></hs-swiper>
        </div>

        <!-- tab选择条 -->
        <div class="tab">
          <hs-tab :selectedIndex="selectedIndex" :options="tabList" @select="_selectTab"></hs-tab>
        </div>

        <div class="section" v-if="tabList[selectedIndex] === $t('base.China') || tabList[selectedIndex] === $t('base.InternationalRegion')">

          <!-- 国内 -->
          <template v-if="tabList[selectedIndex] === $t('base.China')">
            <template v-if="language === 'CN'">
              <div class="item-china">
                <div class="left" @click="[showWhereBack = false, showWhereGo = true]">
                  <p>{{$t('base.DepartureCity')}}</p>
                  <p :class="{placeholder: !flightOrder.DepartName}">{{flightOrder.DepartName ? flightOrder.DepartObject[`AirPortName_${language}`] : $t('base.PleaseChoose')}}</p>
                </div>
                <div class="logo" style="background-image: url('static/image/plane-ticket/query/planefly.png')" :style="{transform: `rotate(${360 * rotate}deg)`}"  @click="_exchangeWhere"></div>
                <div class="right" @click="[showWhereGo = false, showWhereBack = true]">
                  <p>{{$t('base.ArrivalCity')}}</p>
                  <p :class="{placeholder: !flightOrder.ArrivalName}">{{flightOrder.ArrivalName ? flightOrder.ArrivalObject[`AirPortName_${language}`] : $t('base.PleaseChoose')}}</p>
                </div>
              </div>
            </template>
            <template v-else>
              <div class="item-china" :type="language">
                <div class="right">
                  <div class="from" @click="[showWhereBack = false, showWhereGo = true]">
                    <p>{{$t('base.DepartureCity')}}</p>
                    <div>
                      <p :class="{placeholder: !flightOrder.DepartName}">{{flightOrder.DepartName ? flightOrder.DepartObject.AirPortCode : $t('base.PleaseChoose')}}</p>
                      <p v-if="flightOrder.DepartName">{{flightOrder.DepartObject[`AirPortName_${language}`]}}</p>
                    </div>
                  </div>
                  <div class="to" @click="[showWhereGo = false, showWhereBack = true]">
                    <p>{{$t('base.ArrivalCity')}}</p>
                    <div>
                      <p :class="{placeholder: !flightOrder.ArrivalName}">{{flightOrder.ArrivalName ? flightOrder.ArrivalObject.AirPortCode : $t('base.PleaseChoose')}}</p>
                      <p v-if="flightOrder.ArrivalName">{{flightOrder.ArrivalObject[`AirPortName_${language}`]}}</p>
                    </div>
                  </div>
                </div>
                <div class="left">
                  <div class="logo" style="background-image: url('static/image/plane-ticket/query/change.png')" :style="{transform: `rotate(${360 * rotate + 90}deg)`}" @click="_exchangeWhere"></div>
                </div>
              </div>
            </template>

            <div class="item-date">
              <div class="selected" @click="[showWhenBack = false, showWhenGo = true]">
                <p>{{$t(flightOrder.GoSelectedFlight.DepartDate, 'date', ['MM月dd日', 'MM dd'])}}</p>
                <p>{{getWeeks(flightOrder.GoSelectedFlight.DepartDate)}}<template v-if="language === 'CN'">出发</template></p>
              </div>
              <div class="selected" @click="[showWhenGo = false, showWhenBack = true]" v-if="flightOrder.BackSelectedFlight && flightOrder.BackSelectedFlight.DepartDate">
                <p>{{$t(flightOrder.BackSelectedFlight.DepartDate, 'date', ['MM月dd日', 'MM dd'])}}</p>
                <p>{{getWeeks(flightOrder.BackSelectedFlight.DepartDate)}}<template v-if="language === 'CN'">返回</template></p>
                <div class="logo" style="background-image: url('static/image/plane-ticket/query/close.png')" @click.stop="_clearReturnDate"></div>
              </div>
              <div class="return" @click="[showWhenGo = false, showWhenBack = true]" v-else>
                <div class="logo" style="background-image: url('static/image/plane-ticket/query/date.png')"></div>
                <p>{{$t('base.ReturnDate')}}</p>
              </div>
            </div>

            <div class="item-cabin" @click="_showPicker">
              <p>{{flightClass.text}}</p>
              <div class="arrow" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
            </div>
          </template>

          <!-- 国际 -->
          <template v-else-if="tabList[selectedIndex] === $t('base.InternationalRegion')">
            <template v-if="language === 'CN'">
              <div class="item-china">
                <div class="left" @click="[showWhereBackOversea = false, showWhereGoOversea = true]">
                  <p>{{$t('base.DepartureCity')}}</p>
                  <p :class="{placeholder: !intlFlightOrder.SearchParams.Segments[0].DepartureAirportCode}">
                    {{intlFlightOrder.SearchParams.Segments[0].DepartureAirportCode ? intlFlightOrder.SearchParams.Segments[0].DepartObject[`AirPortName_${language}`] : $t('base.PleaseChoose')}}
                  </p>
                </div>
                <div class="logo" style="background-image: url('static/image/plane-ticket/query/planefly.png')" :style="{transform: `rotate(${360 * rotate}deg)`}"  @click="_exchangeWhereOversea"></div>
                <div class="right" @click="[showWhereGoOversea = false, showWhereBackOversea = true]">
                  <p>{{$t('base.ArrivalCity')}}</p>
                  <p :class="{placeholder: !intlFlightOrder.SearchParams.Segments[0].ArrivalAirPortCode}">
                    {{intlFlightOrder.SearchParams.Segments[0].ArrivalAirPortCode ? intlFlightOrder.SearchParams.Segments[0].ArrivalObject[`AirPortName_${language}`] : $t('base.PleaseChoose')}}
                  </p>
                </div>
              </div>
            </template>
            <template v-else>
              <div class="item-china" :type="language">
                <div class="right">
                  <div class="from" @click="[showWhereBackOversea = false, showWhereGoOversea = true]">
                    <p>{{$t('base.DepartureCity')}}</p>
                    <div>
                      <p :class="{placeholder: !intlFlightOrder.SearchParams.Segments[0].DepartureAirportCode}">{{intlFlightOrder.SearchParams.Segments[0].DepartureAirportCode ? intlFlightOrder.SearchParams.Segments[0].DepartureAirportCode : $t('base.PleaseChoose')}}</p>
                      <p v-if="intlFlightOrder.SearchParams.Segments[0].DepartObject">{{intlFlightOrder.SearchParams.Segments[0].DepartObject[`AirPortName_${language}`]}}</p>
                    </div>
                  </div>
                  <div class="to" @click="[showWhereGoOversea = false, showWhereBackOversea = true]">
                    <p>{{$t('base.ArrivalCity')}}</p>
                    <div>
                      <p :class="{placeholder: !intlFlightOrder.SearchParams.Segments[0].ArrivalAirPortCode}">{{intlFlightOrder.SearchParams.Segments[0].ArrivalAirPortCode ? intlFlightOrder.SearchParams.Segments[0].ArrivalAirPortCode : $t('base.PleaseChoose')}}</p>
                      <p v-if="intlFlightOrder.SearchParams.Segments[0].ArrivalObject">{{intlFlightOrder.SearchParams.Segments[0].ArrivalObject[`AirPortName_${language}`]}}</p>
                    </div>
                  </div>
                </div>
                <div class="left">
                  <div class="logo" style="background-image: url('static/image/plane-ticket/query/change.png')" :style="{transform: `rotate(${360 * rotate + 90}deg)`}" @click="_exchangeWhereOversea"></div>
                </div>
              </div>
            </template>

            <div class="item-date">
              <div class="selected" @click="[showWhenBackOversea = false, showWhenGoOversea = true]">
                <p>{{$t(intlFlightOrder.SearchParams.Segments[0].DepartureTime, 'date', ['MM月dd日', 'MM dd'])}}</p>
                <p>{{getWeeks(intlFlightOrder.SearchParams.Segments[0].DepartureTime)}}<template v-if="language === 'CN'">出发</template></p>
              </div>
              <div v-if="intlFlightOrder.SearchParams.Segments[1] && intlFlightOrder.SearchParams.Segments[1].DepartureTime"
                class="selected" @click="[showWhenGoOversea = false, showWhenBackOversea = true]">
                <p>{{$t(intlFlightOrder.SearchParams.Segments[1].DepartureTime, 'date', ['MM月dd日', 'MM dd'])}}</p>
                <p>{{getWeeks(intlFlightOrder.SearchParams.Segments[1].DepartureTime)}}<template v-if="language === 'CN'">返回</template></p>
                <div class="logo" style="background-image: url('static/image/plane-ticket/query/close.png')" @click.stop="_clearIntlReturnDate"></div>
              </div>
              <div class="return" @click="[showWhenGoOversea = false, showWhenBackOversea = true]" v-else>
                <div class="logo" style="background-image: url('static/image/plane-ticket/query/date.png')"></div>
                <p>{{$t('base.ReturnDate')}}</p>
              </div>
            </div>

            <div class="item-cabin" @click="_showIntlPicker">
              <p>{{intlFlightClass.text}}</p>
              <div class="arrow" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
            </div>
          </template>

          <hs-gradient-button style="margin: 20px 0" :title="$t('base.Search')" @click="_gotoQuery"></hs-gradient-button>
        </div>

        <!-- 国际多程 -->
        <template v-if="tabList[selectedIndex] === $t('base.OverseasMultiCity')">
          <div class="section" v-for="(item, index) in intlFlightOrder.SearchParams.Segments" :key="index">

            <div class="multiple-segment">
              <div class="left">{{index + 1}}</div>
              <div class="right">
                <template v-if="language === 'CN'">
                  <div class="item-china">
                    <div class="left" @click="_openAddressWhereMultiple(index, true)">
                      <p>{{$t('base.DepartureCity')}}</p>
                      <p :class="{placeholder: !intlFlightOrder.SearchParams.Segments[index].DepartureAirportCode}">
                        {{intlFlightOrder.SearchParams.Segments[index].DepartureAirportCode ? intlFlightOrder.SearchParams.Segments[index].DepartObject[`AirPortName_${language}`] : $t('base.PleaseChoose')}}
                      </p>
                    </div>
                    <div class="logo" style="background-image: url('static/image/plane-ticket/query/planefly.png')" :style="{transform: `rotate(${360 * (!item.rotate ? 0 : item.rotate)}deg)`}"  @click="_exchangeWhereMultiple(index)"></div>
                    <div class="right" @click="_openAddressWhereMultiple(index, false)">
                      <p>{{$t('base.ArrivalCity')}}</p>
                      <p :class="{placeholder: !intlFlightOrder.SearchParams.Segments[index].ArrivalAirPortCode}">
                        {{intlFlightOrder.SearchParams.Segments[index].ArrivalAirPortCode ? intlFlightOrder.SearchParams.Segments[index].ArrivalObject[`AirPortName_${language}`] : $t('base.PleaseChoose')}}
                      </p>
                    </div>
                  </div>
                </template>
                <template v-else>
                  <div class="item-china" :type="language">
                    <div class="right">
                      <div class="from" @click="_openAddressWhereMultiple(index, true)">
                        <p>{{$t('base.DepartureCity')}}</p>
                        <div>
                          <p :class="{placeholder: !intlFlightOrder.SearchParams.Segments[index].DepartureAirportCode}">{{intlFlightOrder.SearchParams.Segments[index].DepartureAirportCode ? intlFlightOrder.SearchParams.Segments[index].DepartureAirportCode : $t('base.PleaseChoose')}}</p>
                          <p v-if="intlFlightOrder.SearchParams.Segments[index].DepartObject">{{intlFlightOrder.SearchParams.Segments[index].DepartObject[`AirPortName_${language}`]}}</p>
                        </div>
                      </div>
                      <div class="to" @click="_openAddressWhereMultiple(index, false)">
                        <p>{{$t('base.ArrivalCity')}}</p>
                        <div>
                          <p :class="{placeholder: !intlFlightOrder.SearchParams.Segments[index].ArrivalAirPortCode}">{{intlFlightOrder.SearchParams.Segments[index].ArrivalAirPortCode ? intlFlightOrder.SearchParams.Segments[index].ArrivalAirPortCode : $t('base.PleaseChoose')}}</p>
                          <p v-if="intlFlightOrder.SearchParams.Segments[index].ArrivalObject">{{intlFlightOrder.SearchParams.Segments[index].ArrivalObject[`AirPortName_${language}`]}}</p>
                        </div>
                      </div>
                    </div>
                    <div class="left">
                      <div class="logo" style="background-image: url('static/image/plane-ticket/query/change.png')" :style="{transform: `rotate(${360 * (!item.rotate ? 0 : item.rotate) + 90}deg)`}" @click="_exchangeWhereMultiple(index)"></div>
                    </div>
                  </div>
                </template>
                <div class="item-date" @click="_openDateWhenMultiple(index)">
                  <div class="selected" v-if="intlFlightOrder.SearchParams.Segments[index]">
                    <p>{{$t(intlFlightOrder.SearchParams.Segments[index].DepartureTime, 'date', ['MM月dd日', 'MM dd'])}}</p>
                    <p>{{getWeeks(intlFlightOrder.SearchParams.Segments[index].DepartureTime)}}<template v-if="language === 'CN'">出发</template></p>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div class="section" v-if="intlFlightOrder.SearchParams.Segments.length < 6">
            <div class="multiple-add" @click="_addSegmentsMultiple">
              <div class="logo" style="background-image: url('static/image/intl-plane-ticket/query/add.png')"></div>
              <p>再加一程</p>
              <p>至少选择一个国际城市/港澳台</p>
            </div>
          </div>

          <div class="section">
            <div class="item-cabin" @click="_showIntlPicker">
              <p>{{intlFlightClass.text}}</p>
              <div class="arrow" style="background-image: url('static/image/base/cell/right_arrow_gray.png')"></div>
            </div>
            <hs-gradient-button style="margin: 20px 0" :title="$t('base.Search')" @click="_gotoQuery"></hs-gradient-button>
          </div>
        </template>

        <div style="width: 100%;height: 10px;"></div>
      </ul>
    </hs-scroll>
    <!-- 国内 -->
    <hs-address-picker v-model="showWhereGo" :switchList="[$t('base.China')]" :title="$t('base.DepartureCity')" @select="_setAddressWhereGo"></hs-address-picker>
    <hs-address-picker v-model="showWhereBack" :switchList="[$t('base.China')]" :title="$t('base.ArrivalCity')" @select="_setAddressWhereBack"></hs-address-picker>
    <hs-date-picker v-model="showWhenGo" :date="flightOrder.GoSelectedFlight.DepartDate" @changeDate="_setDateWhenGo" :title="$t('base.DepartureDate')" :XOrder="flightOrder" :OrderBusinessType="1"></hs-date-picker>
    <hs-date-picker
      v-model="showWhenBack"
      :date="flightOrder.BackSelectedFlight.DepartDate ? flightOrder.BackSelectedFlight.DepartDate : beTime(new Date(flightOrder.GoSelectedFlight.DepartDate).setDate(new Date(flightOrder.GoSelectedFlight.DepartDate).getDate() + 1))"
      @changeDate="_setDateWhenBack"
      :overdueDate="flightOrder.GoSelectedFlight.DepartDate"
      :title="$t('base.ReturnDate')"
      :XOrder="flightOrder"
      :OrderBusinessType="1">
    </hs-date-picker>
    <!------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------>
    <!-- 国际 -->
    <hs-address-picker v-model="showWhereGoOversea" :switchList="[$t('base.China'), $t('base.InternationalRegion')]" :title="$t('base.DepartureCity')" showSwitchBar :activeIndex.sync="tabActivedIndex" @select="_setAddressWhereGoOversea"></hs-address-picker>
    <hs-address-picker v-model="showWhereBackOversea" :switchList="[$t('base.China'), $t('base.InternationalRegion')]" :title="$t('base.ArrivalCity')" showSwitchBar :activeIndex.sync="tabActivedIndex" @select="_setAddressWhereBackOversea"></hs-address-picker>
    <hs-date-picker v-model="showWhenGoOversea" :date="intlFlightOrder.SearchParams.Segments[0].DepartureTime" @changeDate="_setDateWhenGoOversea" :title="$t('base.DepartureDate')"></hs-date-picker>
    <hs-date-picker
      v-model="showWhenBackOversea"
      :date="(intlFlightOrder.SearchParams.Segments[1] && intlFlightOrder.SearchParams.Segments[1].DepartureTime) ? intlFlightOrder.SearchParams.Segments[1].DepartureTime : beTime(new Date(intlFlightOrder.SearchParams.Segments[0].DepartureTime).setDate(new Date(intlFlightOrder.SearchParams.Segments[0].DepartureTime).getDate() + 1))"
      @changeDate="_setDateWhenBackOversea"
      :overdueDate="intlFlightOrder.SearchParams.Segments[0].DepartureTime"
      :title="$t('base.ReturnDate')">
    </hs-date-picker>
    <!------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------>
    <!-- 国际多段 -->
    <hs-address-picker v-model="showWhereMultiple" :switchList="[$t('base.China'), $t('base.InternationalRegion')]" :title="$t('base.DepartureCity')" showSwitchBar :activeIndex.sync="tabActivedIndex" @select="_setAddressWhereMultiple"></hs-address-picker>
    <hs-date-picker v-if="intlFlightOrder.SearchParams.Segments[whichMultiple.index]"
      v-model="showWhenMultiple"
      :date="intlFlightOrder.SearchParams.Segments[whichMultiple.index].DepartureTime"
      @changeDate="_setDateWhenMultiple"
      :overdueDate="whichMultiple.index >= 1 ? intlFlightOrder.SearchParams.Segments[whichMultiple.index - 1].DepartureTime : beTime(new Date())"
      :title="$t('base.DepartureDate')"></hs-date-picker>
    <!------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------------>
    <c-travel-standard v-model="showTravelStandard" :details="details.FlightRank" :travelPolicy="details.TravelPolicy"></c-travel-standard>
  </div>
</template>
<script type="text/ecmascript-6">
// 机票预订查询页面
import HsScroll from 'base/hs-scroll/hs-scroll'
import CHeader from 'components/c-header/c-header'
import HsSwiper from 'base/hs-swiper/hs-swiper'
import HsTab from 'base/hs-tab/hs-tab'
import HsDatePicker from 'base/hs-date-picker/hs-date-picker'
import HsAddressPicker from 'base/hs-address-picker/hs-address-picker'
import HsGradientButton from 'base/hs-button/hs-gradient-button'
import CTravelStandard from 'components/c-travel-standard/c-travel-standard'
import {mapGetters, mapMutations} from 'vuex'
import {kpiFlightGetFlightIndexInitInfo} from 'api'
export default {
  beforeRouteLeave (to, from, next) {
    if (
      to.path !== '/login' &&
      to.path !== '/home' &&
      to.path !== '/planeTicket/inforList' &&
      to.path !== '/intlPlaneTicket/inforList') {
      next(false)
    } else {
      next()
    }
  },
  created() {
    /** 存TravelType, 若无则返回首页 */
    let flightOrder = {...this.flightOrder}
    flightOrder.TravelType = this.$route.query.TravelType
    this.SET_FLIGHT_ORDER(flightOrder)
    let intlFlightOrder = {...this.intlFlightOrder}
    intlFlightOrder.TravelType = this.$route.query.TravelType
    this.SET_INTL_FLIGHT_ORDER(intlFlightOrder)
    if (this.noValue(this.flightOrder.TravelType)) {
      this.$router.replace('/home')
    }
    this._readlocalStorageWithQuery()
    /*****************************************************/
    this._getFlightRight()
  },
  computed: {
    ...mapGetters([
      'flightOrder',
      'intlFlightOrder'
    ])
  },
  data() {
    return {
      // 轮播图数据
      banners: ['/static/image/plane-ticket/query/banner_01.jpg'],
      tabList: [this.$t('base.China'), this.$t('base.InternationalRegion'), this.$t('base.OverseasMultiCity')],
      /** 差旅标准 */
      showTravelStandard: false,
      details: {},
      /********************************************************************/
      // 旋转切换动效参数
      rotate: 0,
      /** 国内去程返程参数 */
      showWhereGo: false,
      showWhereBack: false,
      showWhenGo: false,
      showWhenBack: false,
      /********************************************************************/
      /** 国际去程返程参数 */
      showWhereGoOversea: false,
      showWhereBackOversea: false,
      showWhenGoOversea: false,
      showWhenBackOversea: false,
      /********************************************************************/
      /** 国际多段参数 */
      showWhereMultiple: false,
      showWhenMultiple: false,
      whichMultiple: {index: 0, from: true},
      /********************************************************************/
      // 地址选择器参数
      tabActivedIndex: 0,
      /********************************************************************/
      // 舱位类型
      flightClass: {text: '', value: undefined},
      intlFlightClass: {text: '', value: undefined},
      /********************************************************************/
      // tab参数
      selectedIndex: 0
    }
  },
  methods: {
    ...mapMutations([
      'SET_FLIGHT_ORDER',
      'SET_INTL_FLIGHT_ORDER'
    ]),
    /** 获取机票差旅标准 */
    _getFlightRight() {
      kpiFlightGetFlightIndexInitInfo().then((res) => {
        console.log('机票权限查询页面：', res)
        this.details = res.ResultData
        this._creatCabinPicker(res.ResultData)
      })
    },
    /** 舱位等级picker */
    _creatCabinPicker(res) {
      // 国内
      let SearchLimits = Number(this.$route.query.TravelType) === 0 ? res.FlightSearchLimits : res.PrivateFlightSearchLimits
      let pickerdata = SearchLimits.map(v => {
        return {
          text: v.Name,
          value: v.Value
        }
      })

      let HISTORY_FLIGHTCLASS = window.localStorage.HISTORY_FLIGHTCLASS ? JSON.parse(window.localStorage.HISTORY_FLIGHTCLASS) : null
      if (HISTORY_FLIGHTCLASS && pickerdata.map(v => v.value).indexOf(HISTORY_FLIGHTCLASS.value) >= 0) {
        this.flightClass = HISTORY_FLIGHTCLASS
      } else {
        this.flightClass = pickerdata[0]
      }

      this.picker = this.$createPicker({
        title: '',
        data: [pickerdata],
        cancelTxt: this.$t('base.Cancel'),
        confirmTxt: this.$t('base.Confirm'),
        onSelect: this._selectPickerHandle
      })

      // 国际
      let HISTORY_INTLFLIGHTCLASS = window.localStorage.HISTORY_INTLFLIGHTCLASS ? JSON.parse(window.localStorage.HISTORY_INTLFLIGHTCLASS) : null
      if (HISTORY_INTLFLIGHTCLASS) {
        this.intlFlightClass = HISTORY_INTLFLIGHTCLASS
      } else {
        this.intlFlightClass = {text: this.$t('base.EconomyClass'), value: 1}
      }
      this.intlPicker = this.$createPicker({
        title: '',
        data: [[
          {text: this.$t('base.EconomyClass'), value: 1},
          {text: this.$t('base.SuperEconomyClass'), value: 2},
          {text: this.$t('base.BusinessClass'), value: 3},
          {text: this.$t('base.FirstClass'), value: 4}
        ]],
        cancelTxt: this.$t('base.Cancel'),
        confirmTxt: this.$t('base.Confirm'),
        onSelect: this._selectIntlPickerHandle
      })
    },
    _showPicker() {
      this.picker.show()
    },
    _showIntlPicker() {
      this.intlPicker.show()
    },
    _selectPickerHandle(val, index, text) {
      console.log(val, index, text)
      this.flightClass = {
        text: text[0],
        value: val[0]
      }
    },
    _selectIntlPickerHandle(val, index, text) {
      console.log(val, index, text)
      this.intlFlightClass = {
        text: text[0],
        value: val[0]
      }
    },
    /*********************************************************************************************************************************************/
    /** 切换tab回调 */
    _selectTab(val) {
      this.selectedIndex = val

      // 如果是国际多段
      if (this.tabList[this.selectedIndex] === this.$t('base.OverseasMultiCity')) {
        // 第二程未选择
        if (!this.intlFlightOrder.SearchParams.Segments[1]) {
          this.intlFlightOrder.SearchParams.Segments[1] = {
            DepartObject: {},
            ArrivalObject: {},
            DepartureTime: this.getNewDate(this.intlFlightOrder.SearchParams.Segments[0].DepartureTime, 0, 0, 3),
            DepartureAirportCode: '',
            ArrivalAirPortCode: ''
          }
          this.whichMultiple.index = 1
          this.whichMultiple.from = true
          this._setAddressWhereMultiple(this.intlFlightOrder.SearchParams.Segments[0].ArrivalObject)
        // 第二程去程未选择
        } else if (
          !this.intlFlightOrder.SearchParams.Segments[1].DepartureAirportCode &&
          !this.intlFlightOrder.SearchParams.Segments[1].ArrivalAirPortCode) {
          this.whichMultiple.index = 1
          this.whichMultiple.from = true
          this._setAddressWhereMultiple(this.intlFlightOrder.SearchParams.Segments[0].ArrivalObject)
        }
      }
    },
    /** 切换出发到达城市 */
    _exchangeWhere() {
      ++this.rotate
      let res = JSON.parse(JSON.stringify(this.flightOrder))
      res.DepartObject = this.flightOrder.ArrivalObject
      res.ArrivalObject = this.flightOrder.DepartObject
      res.DepartCode = this.flightOrder.ArrivalCode
      res.ArrivalCode = this.flightOrder.DepartCode
      res.DepartName = this.flightOrder.ArrivalName
      res.ArrivalName = this.flightOrder.DepartName
      res.DepartCityName = this.flightOrder.ArrivalCityName
      res.ArrivalCityName = this.flightOrder.DepartCityName
      res.DepartCityCode = this.flightOrder.ArrivalCityCode
      res.ArrivalCityCode = this.flightOrder.DepartCityCode
      res.DepartGeoType = this.flightOrder.ArrivalGeoType
      res.ArrivalGeoType = this.flightOrder.DepartGeoType
      this.SET_FLIGHT_ORDER(res)
    },
    _exchangeWhereOversea() {
      ++this.rotate
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      res.SearchParams.Segments[0].DepartObject = this.intlFlightOrder.SearchParams.Segments[0].ArrivalObject
      res.SearchParams.Segments[0].ArrivalObject = this.intlFlightOrder.SearchParams.Segments[0].DepartObject
      res.SearchParams.Segments[0].DepartureAirportCode = this.intlFlightOrder.SearchParams.Segments[0].ArrivalAirPortCode
      res.SearchParams.Segments[0].ArrivalAirPortCode = this.intlFlightOrder.SearchParams.Segments[0].DepartureAirportCode
      this.SET_INTL_FLIGHT_ORDER(res)
    },
    _exchangeWhereMultiple(index) {
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      let rotate = res.SearchParams.Segments[index].rotate
      res.SearchParams.Segments[index].rotate = !rotate ? 1 : (rotate + 1)
      res.SearchParams.Segments[index].DepartObject = this.intlFlightOrder.SearchParams.Segments[index].ArrivalObject
      res.SearchParams.Segments[index].ArrivalObject = this.intlFlightOrder.SearchParams.Segments[index].DepartObject
      res.SearchParams.Segments[index].DepartureAirportCode = this.intlFlightOrder.SearchParams.Segments[index].ArrivalAirPortCode
      res.SearchParams.Segments[index].ArrivalAirPortCode = this.intlFlightOrder.SearchParams.Segments[index].DepartureAirportCode
      this.SET_INTL_FLIGHT_ORDER(res)
    },
    /*********************************************************************************************************************************************************/
    /** 国内 选择地址和日期 */
    _setDateWhenGo(val) {
      let res = JSON.parse(JSON.stringify(this.flightOrder))
      res.GoSelectedFlight.DepartDate = val
      this.SET_FLIGHT_ORDER(res)
      /** 如果选择的去程日期小于今天, 则自动再去选择明天这个日期 */
      if (this.flightOrder.GoSelectedFlight.DepartDate && (this.flightOrder.GoSelectedFlight.DepartDate < this.beTime(new Date()))) {
        this._setDateWhenGo(this.getNewDate(this.beTime(new Date()), 0, 0, 1))
      }
      /** 如果选择的去程日期大于返程日期, 则自动修改返程日期为去程日期+1 */
      if (this.flightOrder.BackSelectedFlight.DepartDate && (this.flightOrder.GoSelectedFlight.DepartDate > this.flightOrder.BackSelectedFlight.DepartDate)) {
        this._setDateWhenBack(this.beTime(new Date(val).setDate(new Date(val).getDate() + 1)))
      }
      /*********************************************************************************************************************************************************/
    },
    _setDateWhenBack(val) {
      let res = JSON.parse(JSON.stringify(this.flightOrder))
      res.BackSelectedFlight.DepartDate = val
      this.SET_FLIGHT_ORDER(res)
    },
    _setAddressWhereGo(val) {
      let res = {...this.flightOrder}
      res.DepartObject = val
      res.DepartCode = val.AirPortCode
      res.DepartName = val[`AirPortName_${this.language}`]
      res.DepartCityName = val[`CityName_${this.language}`]
      res.DepartCityCode = val.CityCode
      res.DepartGeoType = val.GeoType
      this.SET_FLIGHT_ORDER(res)
    },
    _setAddressWhereBack(val) {
      let res = {...this.flightOrder}
      res.ArrivalObject = val
      res.ArrivalCode = val.AirPortCode
      res.ArrivalName = val[`AirPortName_${this.language}`]
      res.ArrivalCityName = val[`CityName_${this.language}`]
      res.ArrivalCityCode = val.CityCode
      res.ArrivalGeoType = val.GeoType
      this.SET_FLIGHT_ORDER(res)
    },
    /**************************************************************************************************/
    /** 国际 选择地址和日期 */
    _setDateWhenGoOversea(val) {
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      res.SearchParams.Segments[0].DepartureTime = val
      this.SET_INTL_FLIGHT_ORDER(res)
      /** 如果选择的去程日期小于今天, 则自动再去选择明天这个日期 */
      if ((this.intlFlightOrder.SearchParams.Segments[0] && this.intlFlightOrder.SearchParams.Segments[0].DepartureTime) && (this.intlFlightOrder.SearchParams.Segments[0].DepartureTime < this.beTime(new Date()))) {
        this._setDateWhenGoOversea(this.getNewDate(this.beTime(new Date()), 0, 0, 1))
      }
      /** 如果选择的去程日期大于返程日期, 则自动修改返程日期为去程日期+1 */
      if ((this.intlFlightOrder.SearchParams.Segments[1] && this.intlFlightOrder.SearchParams.Segments[1].DepartureTime) && (this.intlFlightOrder.SearchParams.Segments[0].DepartureTime > this.intlFlightOrder.SearchParams.Segments[1].DepartureTime)) {
        this._setDateWhenBackOversea(this.beTime(new Date(val).setDate(new Date(val).getDate() + 1)))
      }
      /*********************************************************************************************************************************************************/
    },
    _setDateWhenBackOversea(val) {
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      res.SearchParams.Segments[1] = {
        DepartObject: {}, // 前端用于显示, 自定义的出发对象
        ArrivalObject: {}, // 前端用于显示, 自定义的到达对象
        DepartureTime: '',
        DepartureAirportCode: '',
        ArrivalAirPortCode: ''
      }
      res.SearchParams.Segments[1].DepartureTime = val
      this.SET_INTL_FLIGHT_ORDER(res)
    },
    _setAddressWhereGoOversea(val) {
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      res.SearchParams.Segments[0].DepartObject = val
      res.SearchParams.Segments[0].DepartureAirportCode = val.AirPortCode
      this.SET_INTL_FLIGHT_ORDER(res)
    },
    _setAddressWhereBackOversea(val) {
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      res.SearchParams.Segments[0].ArrivalObject = val
      res.SearchParams.Segments[0].ArrivalAirPortCode = val.AirPortCode
      this.SET_INTL_FLIGHT_ORDER(res)
    },
    /**************************************************************************************************/
    /** 国际多段 选择地址和日期 */
    _openDateWhenMultiple(index) {
      this.whichMultiple.index = index
      this.showWhenMultiple = true
    },
    _openAddressWhereMultiple(index, from) {
      this.whichMultiple.index = index
      this.whichMultiple.from = from
      this.showWhereMultiple = true
    },
    _setDateWhenMultiple(val) {
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      res.SearchParams.Segments[this.whichMultiple.index].DepartureTime = val

      // 校验日期
      res.SearchParams.Segments.forEach((element, index) => {
        if (index === 0) {
          if (res.SearchParams.Segments[index].DepartureTime < this.beTime(new Date())) {
            res.SearchParams.Segments[index].DepartureTime = this.getNewDate(this.beTime(new Date()), 0, 0, 1)
          }
        } else {
          if (res.SearchParams.Segments[index].DepartureTime < res.SearchParams.Segments[index - 1].DepartureTime) {
            res.SearchParams.Segments[index].DepartureTime = this.getNewDate(res.SearchParams.Segments[index - 1].DepartureTime, 0, 0, 3)
          }
        }
      })

      this.SET_INTL_FLIGHT_ORDER(res)
    },
    _setAddressWhereMultiple(val) {
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      if (this.whichMultiple.from) {
        res.SearchParams.Segments[this.whichMultiple.index].DepartObject = val
        res.SearchParams.Segments[this.whichMultiple.index].DepartureAirportCode = val.AirPortCode
      } else {
        res.SearchParams.Segments[this.whichMultiple.index].ArrivalObject = val
        res.SearchParams.Segments[this.whichMultiple.index].ArrivalAirPortCode = val.AirPortCode
      }
      this.SET_INTL_FLIGHT_ORDER(res)
    },
    _addSegmentsMultiple() {
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      let length = res.SearchParams.Segments.length

      // 检验之前的行程选择是否完成
      if (!res.SearchParams.Segments[length - 1].DepartureAirportCode) {
        this.$toast(`请完善第${length}程的出发城市`)
        return
      } else if (!res.SearchParams.Segments[length - 1].ArrivalAirPortCode) {
        this.$toast(`请完善第${length}程的到达城市`)
        return
      } else if (res.SearchParams.Segments[length - 1].DepartObject.CityCode === res.SearchParams.Segments[length - 1].ArrivalObject.CityCode) {
        this.$toast(this.$t('TM.DepartureCityAndArrivalCityCannotBeSame'))
        return
      }

      res.SearchParams.Segments[length] = {
        DepartObject: res.SearchParams.Segments[length - 1].ArrivalObject,
        ArrivalObject: {},
        DepartureTime: this.getNewDate(this.intlFlightOrder.SearchParams.Segments[length - 1].DepartureTime, 0, 0, 3),
        DepartureAirportCode: res.SearchParams.Segments[length - 1].ArrivalAirPortCode,
        ArrivalAirPortCode: ''
      }
      this.SET_INTL_FLIGHT_ORDER(res)
    },
    /**************************************************************************************************/
    /** 清除返程日期 */
    _clearReturnDate() {
      let res = {...this.flightOrder}
      res.BackSelectedFlight = {
        FlightNo: '',
        CabinID: '',
        DepartDate: '',
        FlightDetails: {},
        CabinDetails: {}
      }
      this.SET_FLIGHT_ORDER(res)
    },
    _clearIntlReturnDate() {
      let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
      if (res.SearchParams.Segments[1]) {
        res.SearchParams.Segments = res.SearchParams.Segments.slice(0, 1)
      }
      this.SET_INTL_FLIGHT_ORDER(res)
    },
    /**************************************************************************************************/
    /** 机票查询 */
    _gotoQuery() {
      // 国内
      if (this.tabList[this.selectedIndex] === this.$t('base.China')) {
        if (this.noValue(this.flightOrder.DepartCityCode)) {
          this.$toast(this.$t('TM.PleaseSelectTheDepartureCity'))
          return
        } else if (this.noValue(this.flightOrder.ArrivalCityCode)) {
          this.$toast(this.$t('TM.PleaseSelectArrivalCity'))
          return
        } else if (this.flightOrder.DepartCityName === this.flightOrder.ArrivalCityName) {
          this.$toast(this.$t('TM.DepartureCityAndArrivalCityCannotBeSame'))
          return
        }
        let isOneWay = !(this.flightOrder.BackSelectedFlight && this.flightOrder.BackSelectedFlight.DepartDate)
        this.$router.push({path: '/planeTicket/inforList', query: {FlightClass: this.flightClass.value, isOneWay: isOneWay}})

      // 国际
      } else if (this.tabList[this.selectedIndex] === this.$t('base.InternationalRegion')) {
        let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
        if (this.intlFlightOrder.SearchParams.Segments[1] && this.intlFlightOrder.SearchParams.Segments[1].DepartureTime) {
          res.SearchParams.Segments[1].DepartObject = this.intlFlightOrder.SearchParams.Segments[0].ArrivalObject
          res.SearchParams.Segments[1].ArrivalObject = this.intlFlightOrder.SearchParams.Segments[0].DepartObject
          res.SearchParams.Segments[1].DepartureAirportCode = this.intlFlightOrder.SearchParams.Segments[0].ArrivalAirPortCode
          res.SearchParams.Segments[1].ArrivalAirPortCode = this.intlFlightOrder.SearchParams.Segments[0].DepartureAirportCode
          res.SearchParams.Segments = res.SearchParams.Segments.slice(0, 2)
        } else {
          res.SearchParams.Segments = res.SearchParams.Segments.slice(0, 1)
        }

        res.SearchParams.FlightClass = this.intlFlightClass.value
        this.SET_INTL_FLIGHT_ORDER(res)

        // 本地校验
        if (!this._intlLocalCheck()) return

        this.$router.push({path: '/intlPlaneTicket/inforList'})

      // 国际多段
      } else if (this.tabList[this.selectedIndex] === this.$t('base.OverseasMultiCity')) {
        // 本地校验
        if (!this._intlLocalCheck()) return

        let res = JSON.parse(JSON.stringify(this.intlFlightOrder))
        res.SearchParams.FlightClass = this.intlFlightClass.value
        this.SET_INTL_FLIGHT_ORDER(res)
        this.$router.push({path: '/intlPlaneTicket/inforList'})
      }
      this._savelocalStorageWithQuery()
    },

    /** 国际机票本地校验 */
    _intlLocalCheck() {
      /** 是否通过本地校验 */
      let pass = true
      /** 请至少选择一个国际城市/港澳台 */
      let haveIntlFlight = false
      /** 有未完善的行程 */
      let noValueFlight = false
      /** 出发城市和到达城市不得相同 */
      let sameAirportCode = false

      for (let item of this.intlFlightOrder.SearchParams.Segments) {
        if (item.DepartObject.CountryType === '国际' || item.ArrivalObject.CountryType === '国际') {
          haveIntlFlight = true
        }
        if (!item.DepartureAirportCode || !item.ArrivalAirPortCode) {
          noValueFlight = true
        }
        if (item.DepartObject.CityCode === item.ArrivalObject.CityCode) {
          sameAirportCode = true
        }
      }
      if (!haveIntlFlight) {
        this.$toast('至少选择一个国际城市/港澳台')
        pass = false
        return
      } else if (noValueFlight) {
        this.$toast('请完善您的行程')
        pass = false
        return
      } else if (sameAirportCode) {
        this.$toast(this.$t('TM.DepartureCityAndArrivalCityCannotBeSame'))
        pass = false
        return
      }
      return pass
    },

    /** 读写localStorage内的已选项目 */
    _savelocalStorageWithQuery() {
      // 国内
      if (this.tabList[this.selectedIndex] === this.$t('base.China')) {
        window.localStorage.HISTORY_QUERY_DOMESTIC_PLANE = JSON.stringify({
          goWhere: this.flightOrder.DepartObject,
          backWhere: this.flightOrder.ArrivalObject,
          goWhen: this.flightOrder.GoSelectedFlight.DepartDate,
          backWhen: this.flightOrder.BackSelectedFlight.DepartDate
        })

        window.localStorage.HISTORY_FLIGHTCLASS = JSON.stringify(this.flightClass)

      // 国际
      } else if (
        this.tabList[this.selectedIndex] === this.$t('base.InternationalRegion') ||
        this.tabList[this.selectedIndex] === this.$t('base.OverseasMultiCity')
      ) {
        window.localStorage.HISTORY_QUERY_INTL_PLANE = JSON.stringify({
          goWhere: this.intlFlightOrder.SearchParams.Segments[0].DepartObject,
          backWhere: this.intlFlightOrder.SearchParams.Segments[0].ArrivalObject,
          goWhen: this.intlFlightOrder.SearchParams.Segments[0].DepartureTime,
          backWhen: (this.intlFlightOrder.SearchParams.Segments[1] && this.intlFlightOrder.SearchParams.Segments[1].DepartureTime) ? this.intlFlightOrder.SearchParams.Segments[1].DepartureTime : ''
        })

        window.localStorage.HISTORY_INTLFLIGHTCLASS = JSON.stringify(this.intlFlightClass)
      }
    },
    _readlocalStorageWithQuery() {
      if (window.localStorage.HISTORY_QUERY_DOMESTIC_PLANE) {
        let res = JSON.parse(window.localStorage.HISTORY_QUERY_DOMESTIC_PLANE)
        this._setAddressWhereGo(res.goWhere)
        this._setAddressWhereBack(res.backWhere)
        this._setDateWhenGo(res.goWhen)
        if (res.backWhen && res.backWhen > this.flightOrder.GoSelectedFlight.DepartDate) {
          this._setDateWhenBack(res.backWhen)
        }
      }
      if (window.localStorage.HISTORY_QUERY_INTL_PLANE) {
        let res = JSON.parse(window.localStorage.HISTORY_QUERY_INTL_PLANE)
        this._setAddressWhereGoOversea(res.goWhere)
        this._setAddressWhereBackOversea(res.backWhere)
        this._setDateWhenGoOversea(res.goWhen)
        if (res.backWhen && res.backWhen > this.intlFlightOrder.SearchParams.Segments[0].DepartureTime) {
          this._setDateWhenBackOversea(res.backWhen)
        }
      }
    }
    /*********************************************************************************************************/
  },
  components: {
    HsScroll,
    CHeader,
    HsSwiper,
    HsTab,
    HsDatePicker,
    HsAddressPicker,
    HsGradientButton,
    CTravelStandard
  }
}
</script>
